.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.heading {
  margin-top: 100px;
  margin-bottom: 25 px;
  font-family: Gilmer, sans-serif;
  color: #000;
  font-size: 68px;
  line-height: 68px;
  font-weight: 700;
  text-align: center;
}
.headingText {
  font-family: Gilmer, sans-serif;
  color: #000;
  font-size: 22px;
  line-height: 28px;
  text-align: center;
}
.heading2 {
  margin-bottom: 15px;
  font-family: Gilmer, sans-serif;
  color: #000;
  font-size: 36px;
  line-height: 44px;
  font-weight: 500;
  text-align: center;
}
.section {
  padding-top: 5rem;
  padding-bottom: 7rem;
}
.blogBtn {
  color: #fff;
  background-color: #00917c;
}
.footerLink {
  text-decoration: none;
  position: relative;
  display: inline-block;
  color: rgba(0,0,0,.5);
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
}
.popup-inner {
  position: relative;
  padding: 32px;
  width: 100%;
  max-width: 640px;
  background-color: #fff;
}
.popup-inner .close-btn {
  position: absolute;
  top: 16px;
  right: 16px;
}

.query {
  border: 1px solid #b2b0b0;
  width: 60%;
  padding: 34px 0;
  margin: 100px 0;
  justify-content: space-evenly;
  flex-wrap: wrap;
  border-radius: 14px;
}
.query p {
  font-size: 20px;
}
.cardBorder {
  border: 1px solid gray;
  border-radius: 9px;
}

.titleStyle {
  color: #00917c;
}
.details {
  width: 600px;
  border: 1px solid #ddd;
  margin: 15px;
  padding: 23px;
  text-align: center;
  margin-left: 630px;
}
.details > div {
  margin: 15px;
  font-weight: 200;
}
.details > h3 {
  border-bottom: 1px solid #ccc;
  padding-bottom: 15px;
}
.errorMsg {
  padding: 5px;
  margin: 0px;
  font-size: 11px;
  color: red;
}

.card {
    max-width: 500px;
    margin: auto;
    color: black;
    border-radius: 20 px
}

p {
    margin: 0px
}

.container .h8 {
    font-size: 30px;
    font-weight: 800;
    text-align: center
}

.btnpay.btn.btn-primary {
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    background-image: linear-gradient(to right, #77A1D3 0%, #79CBCA 51%, #77A1D3 100%);
    border: none;
    transition: 0.5s;
    background-size: 200% auto
}

.btnpay.btn.btn.btn-primary:hover {
    background-position: right center;
    color: #fff;
    text-decoration: none
}

.btnpay.btn.btn-primary:hover .fas.fa-arrow-right {
    transform: translate(15px);
    transition: transform 0.2s ease-in
}

.payformStyle.form-control {
    color: white;
    background-color: #223C60;
    border: 2px solid transparent;
    height: 60px;
    padding-left: 20px;
    vertical-align: middle
}

.payformStyle.form-control:focus {
    color: white;
    background-color: #0C4160;
    border: 2px solid #2d4dda;
    box-shadow: none
}

.text {
    font-size: 14px;
    font-weight: 600
}

:-ms-input-placeholder {
    font-size: 14px;
    font-weight: 600
}

::placeholder {
    font-size: 14px;
    font-weight: 600
}
